<template>
  <div class="home" >
      Home
      <button @mouseover="mouseover" @mouseleave="mouseleave" :style="style" >test</button>

  </div>
</template>

<script>
// @ is an alias to /src
export default {
  
  name: 'home',
  data () {
    return {
      style: ''
    }
  },
  methods: {
    mouseover(){
      this.style = 'color: blue;'
    },
    mouseleave(){
      this.style = ''
    }
  }
}
</script>