<template>
  <b-container>
    <div>
      <b-row>
        <b-col lg="12" style="margin-top:15px; margin-bottom:15px">
          <b-button
            class="float-right"
            variant="primary"
            @click="updateCategory('add', {})"
          >Tilføj kategori</b-button>
          <b-row>
            <b-col lg="8" style="margin-bottom:10px">
              <b-button :disabled="isFetching" v-on:click="getCategories">Opdater tabel</b-button>
              <b-button v-show="isFetching" v-on:click="cancel">Cancel</b-button>
            </b-col>
            <b-col lg="4">
              <b-input-group>
                <b-form-input
                  v-model="search"
                  type="search"
                  id="filterInput"
                  placeholder="Skriv for at søge..."
                ></b-form-input>
              </b-input-group>
            </b-col>
          </b-row>

          <div class="tablecontainer" style="text-align:center">
            <b-table
              id="categoryTable"
              outlined
              :busy="isFetching"
              :items="Items"
              :fields="fields"
              :filter="search"
              show-empty
              :per-page="perPage"
              :current-page="currentPage"
              primary-key="name"
              @filtered="onFilter"
              :sort-by.sync="sortBy"
            >
              <template v-slot:head(button)>
                <b-button
                  style="width:100% margin-left:auto margin-right:auto"
                  @click="sortBy = ''; search = ''"
                >Nulstil filter</b-button>
              </template>
              <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                  <b-spinner class="align-middle"></b-spinner>
                  <strong>Loading...</strong>
                </div>
              </template>
              <template v-slot:empty>
                <h4>{{notFoundMessage}}</h4>
                <b-button v-on:click="getCategories">Refresh table</b-button>
              </template>
              <template v-slot:cell(button)="data">
                <div>
                  <b-row>
                    <b-col lg="6">
                      <b-button variant="warning" @click="updateCategory('edit', data.item)">Rediger</b-button>
                    </b-col>
                    <b-col lg="6">
                      <b-button variant="danger" @click="updateCategory('delete', data.item)">Slet</b-button>
                    </b-col>
                  </b-row>
                </div>
              </template>
            </b-table>
          </div>
          <b-pagination
            align="center"
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            aria-controls="categoryTable"
          />
        </b-col>
      </b-row>
    </div>

    <b-modal
      id="updateCategory"
      v-model="showUpdateCategoryModal"
      cancel-title="Annuller"
      no-close-on-backdrop
      size="lg"
      :title="updateCategoryModalTitle"
    >
      <b-form v-if="activeModal != 'delete'">
        <b-row>
          <b-col lg="12" style="margin-bottom:15px">
            <label>Kategorinavn</label>
            <b-form-input
              v-if="activeModal == 'add'"
              v-model="categoryName"
              type="text"
              required
              placeholder="Kategorinavn..."
            ></b-form-input>
            <b-form-input
              v-if="activeModal == 'edit'"
              v-model="selectedCategory.categoryName"
              type="text"
              required
              placeholder="Kategorinavn..."
            ></b-form-input>
          </b-col>
        </b-row>
      </b-form>

      <b-row v-if="activeModal == 'delete'">
        <b-col lg="12">
          <p
            style="font-size:20px"
          >Er du sikker på at du vil slette {{selectedCategory.categoryName}} fra listen over kategorier?</p>
        </b-col>
      </b-row>

      <template v-slot:modal-footer>
        <b-button class="float-right" @click="closeModal">Annuller</b-button>
        <b-button
          v-if="activeModal == 'add'"
          style="margin-left:10px"
          class="float-right"
          variant="success"
          @click="addCategory"
        >Gem</b-button>

        <b-button
          v-if="activeModal == 'edit'"
          style="margin-left:10px"
          class="float-right"
          variant="success"
          @click="editCategory"
        >Gem</b-button>

        <b-button
          v-if="activeModal == 'delete'"
          style="margin-left:10px"
          class="float-right"
          variant="danger"
          @click="deleteCategory"
        >Ja</b-button>
      </template>
    </b-modal>
  </b-container>
</template>

<script>
import smoothReflow from "vue-smooth-reflow";

var timer;

export default {
  name: "CategoryManagement",
  mixins: [smoothReflow],
  data() {
    return {
      categoryName: "",
      selectedCategory: {},

      updateCategoryModalTitle: "",
      showUpdateCategoryModal: false,

      fields: [
        { key: "categoryName", label: "Navn" },
        { key: "button", label: "" }
      ],
      isFetching: true,
      failedFetches: 0,
      search: "",
      currentPage: 1,
      totalRows: 0,
      perPage: 10,
      sortBy: "",
      activeModal: "",
      Items: Array,
      notFoundMessage: ""
    };
  },
  computed: {
    Categories() {
      return this.$store.getters["categoryManagement/getCategories"];
    },

    CategoriesAmount() {
      return this.$store.getters["categoryManagement/getCategories"].length;
    }
  },
  methods: {
    getCategories() {
      this.isFetching = true;
      this.$store
        .dispatch("categoryManagement/getCategories")
        .then(() => {
          // eslint-disable-next-line no-console
          console.log(
            "Got some data, now lets show something in this component"
          );
          this.totalRows = this.CategoriesAmount;
          this.Items = this.Categories;
          this.isFetching = false;
          this.failedFetches = 0;
        })
        .catch(() => {
          // eslint-disable-next-line no-console
          console.log("no items found");
          this.Items = [];
          this.totalRows = 0;
          this.failedFetches++;
          if (this.failedFetches <= 5) {
            timer = setTimeout(() => {
              this.getItems();
            }, 5000);
          } else {
            this.isFetching = false;
            this.failedFetches = 0;
            this.notFoundMessage =
              "Der blev ikke fundet nogen ting i lageret...";
          }
        });
    },
    updateCategory(modalToOpen, item) {
      if (modalToOpen == "add") {
        this.updateCategoryModalTitle = "Tilføj Kategori";
        this.activeModal = "add";
      } else if (modalToOpen == "edit") {
        this.updateCategoryModalTitle = "Rediger Kategori";
        this.activeModal = "edit";
        Object.assign(this.selectedCategory, item);
      } else {
        this.updateCategoryModalTitle = "Slet Kategori";
        this.activeModal = "delete";
        this.selectedCategory = item
        // Object.assign(this.selectedCategory, item);
      }

      this.showUpdateCategoryModal = true;
    },
    addCategory() {
      this.$store
        .dispatch("categoryManagement/addCategory", {
          categoryName: this.categoryName
        })
        .then(() => {
          this.categoryName = "";
          this.showUpdateCategoryModal = false;
          this.getCategories();
        })
        .catch(reject => {
          alert(reject);
        });
    },
    fillEditCategoryModal(item) {
      Object.assign(this.selectedCategory, item);
      this.updateCategory("edit");
    },
    editCategory() {
      this.$store
        .dispatch("categoryManagement/editCategory", {
          categoryId: this.selectedCategory.categoryId,
          categoryName: this.selectedCategory.categoryName
        })
        .then(() => {
          this.getCategories();
          this.showUpdateCategoryModal = false;
        })
        .catch(reject => {
          alert(reject);
          this.getCategories();
          this.showUpdateCategoryModal = false;
        });
    },
    deleteCategory() {
      this.$store
        .dispatch("categoryManagement/deleteCategory", {
          categoryId: this.selectedCategory.categoryId
        })
        .then(() => {
          this.totalRows--;
          this.selectedCategory = {};
          this.showUpdateCategoryModal = false;
        }).catch(reject => {
          alert(reject);
          this.getCategories();
          this.showUpdateCategoryModal = false;
        });
    },
    closeModal() {
      this.categoryName = "";
      this.selectedCategory = {};
      this.activeModal = "";
      this.showUpdateCategoryModal = false;
    },
    cancel() {
      clearTimeout(timer);
      this.failedFetches = 0;
      this.isFetching = false;
    },
    onFilter(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  created() {
    this.getCategories();
  },
  mounted() {
    this.$smoothReflow({
      el: "#categoryTable"
    });
    this.$smoothReflow({
      el: ".tablecontainer"
    });
  }
};
</script>