<template>
<b-nav  vertical class="wrapper">
   <b-button @click="isSidebarExpanded = !isSidebarExpanded">{{ isSidebarExpanded ? '-' : '+'}}</b-button>
    <div :class="sidebarClasses" class="sidebar-wrapper" >
        <b-nav-item exact-active-class="active" to="/management" >{{DepartmnetName}}</b-nav-item>
        <b-nav-item exact-active-class="active" to="/management/AddUsersToDepartment" >Tilføj til afdeling</b-nav-item>
      <b-nav-item exact-active-class="active" to="/management/CategoryManagement">Kategorier</b-nav-item>
      <b-nav-item exact-active-class="active" to="/management/LoanManagement">Udlån</b-nav-item>
      <b-nav-item exact-active-class="active" to="/management/ConsumptionManagement">Forbrug</b-nav-item>
    </div>
    
</b-nav>
</template>
<script>

export default {
  name: 'sidebar',
  data() {
    return {
      isSidebarExpanded: false,
    } 
  },
  computed: {
    sidebarClasses(){
      let classes = []
      if (this.isSidebarExpanded) {
        classes.push("expanded")
      }
      return classes
    },
    
      DepartmnetName(){
        return this.$store.state.auth.user.departmentName

      }
  }
}
</script>