<template>
  <b-container>
    <div>
      <b-row>
        <b-col lg="12" style="margin-top:15px; margin-bottom:15px">
          <b-button
            class="float-right"
            variant="primary"
            v-b-modal.addItemModal
          >Tilføj ting til lager</b-button>
          <b-row>
            <b-col lg="8" style="margin-bottom:10px">
              <b-button :disabled="isFetching" v-on:click="getItems">Opdater tabel</b-button>
              <b-button v-show="isFetching" v-on:click="cancel">Cancel</b-button>
            </b-col>
            <b-col lg="4">
              <b-input-group>
                <b-form-input
                  v-model="search"
                  type="search"
                  id="filterInput"
                  placeholder="Skriv for at søge..."
                ></b-form-input>
              </b-input-group>
            </b-col>
          </b-row>

          <div class="tablecontainer" style="text-align:center">
            <b-table
              id="inventoryTable"
              outlined
              :busy="isFetching"
              :items="Items"
              :fields="fields"
              :filter="search"
              show-empty
              :per-page="perPage"
              :current-page="currentPage"
              primary-key="name"
              @filtered="onFilter"
              :sort-by.sync="sortBy"
            >
              <template v-slot:head(button)>
                <b-button
                  style="width:100% margin-left:auto margin-right:auto"
                  @click="sortBy = ''; search = ''"
                >Nulstil filter</b-button>
              </template>
              <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                  <b-spinner class="align-middle"></b-spinner>
                  <strong>Loading...</strong>
                </div>
              </template>
              <template v-slot:empty>
                <h4>{{notFoundMessage}}</h4>
                <b-button v-on:click="getItems">Refresh table</b-button>
              </template>
              <template v-slot:cell(image)="data">
                <b-img class="imgThumbnail" :src="data.item.image.imageUri"></b-img>
              </template>
              <template v-slot:cell(amountLeft)="data">
                <p v-if="data.item.amountLeft > 25">{{ data.item.amountLeft }}</p>
                <p
                  v-if="data.item.amountLeft < 26 && data.item.amountLeft > 5"
                  style="color:darkOrange"
                >{{ data.item.amountLeft }}</p>
                <p v-if="data.item.amountLeft < 6" style="color:red">{{ data.item.amountLeft }}</p>
              </template>
              <template v-slot:cell(button)="data">
                <div>
                  <b-row>
                    <b-col lg="3">
                      <b-button
                        variant="primary"
                        @click="selectedItemModal(data.item, 'About')"
                      >Mere</b-button>
                    </b-col>
                    <b-col lg="3">
                      <b-button variant="warning" @click="fillEditItemModal(data.item)">Rediger</b-button>
                    </b-col>
                    <b-col lg="3">
                      <b-button variant="warning" @click="addBarcodeToItem(data.item)">Stregkoder</b-button>
                    </b-col>
                    <b-col lg="3">
                      <b-button
                        variant="danger"
                        @click="selectedItemModal(data.item, 'Delete')"
                        :disabled="data.item.barcodes.length == 0"
                      >Slet</b-button>
                    </b-col>
                  </b-row>
                </div>
              </template>
            </b-table>
          </div>
          <b-pagination
            align="center"
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            aria-controls="inventoryTable"
          />
        </b-col>
      </b-row>
    </div>
    <b-modal
      id="addItemModal"
      v-model="showAddItemModal"
      cancel-title="Annuller"
      @cancel="closeModal"
      @ok="addItem"
      no-close-on-backdrop
      size="lg"
      title="Tilføj ting til lager"
    >
      <b-form>
        <b-row>
          <b-col lg="6" style="margin-bottom:15px">
            <label>Mærke</label>
            <b-form-input v-model="itemBrand" type="text" required placeholder="Mærke"></b-form-input>
          </b-col>
          <b-col lg="6">
            <label>Model</label>
            <b-form-input v-model="itemModel" type="text" required placeholder="Model"></b-form-input>
          </b-col>
          <b-col lg="6" style="margin-bottom:15px">
            <label>Beskrivelse</label>
            <b-form-input v-model="itemDescription" type="text" placeholder="Beskrivelse"></b-form-input>
          </b-col>
          <b-col lg="6">
            <label>Kategori</label>
            <b-form-select
              v-model="dropdownSelected"
              :options="Categories"
              value-field="id"
              text-field="value"
            ></b-form-select>
          </b-col>
          <b-col lg="4">
            <b-button style="width:130px" v-b-modal.selectImageModal>Vælg billede</b-button>
            <br />
            <b-button style="width:130px; margin-top:15px" v-b-modal.UploadImageModal>Upload billede</b-button>
          </b-col>
          <b-col lg="2">
            <b-img style="height:105px" fluid :src="SelectedImage.imageUri"></b-img>
          </b-col>
        </b-row>
      </b-form>
    </b-modal>

    <b-modal
      id="MoreAboutItem"
      v-model="showMoreAboutItem"
      cancel-title="Annuller"
      @close="closeModal"
      @ok="closeModal"
      ok-only
      no-close-on-backdrop
      size="lg"
      :title="selectedItemModalTitle"
    >
      <b-form>
        <b-row>
          <b-col lg="6" style="margin-bottom:15px">
            <label style="font-weight:bold">Mærke</label>
            <br />
            <label>{{SelectedItem.brand}}</label>
          </b-col>
          <b-col lg="6">
            <label style="font-weight:bold">Model</label>
            <br />
            <label>{{SelectedItem.model}}</label>
          </b-col>
          <b-col lg="6" style="margin-bottom:15px">
            <label style="font-weight:bold">Beskrivelse</label>
            <br />
            <label>{{SelectedItem.description}}</label>
          </b-col>
          <b-col lg="6">
            <label style="font-weight:bold">Kategori</label>
            <br />
            <label>{{SelectedItem.category.categoryName}}</label>
          </b-col>
          <b-col lg="2">
            <b-img style="height:105px" fluid :src="SelectedImage.imageUri"></b-img>
          </b-col>
          <b-col lang="4"></b-col>
          <b-col lg="6">
            <label style="font-weight:bold">Mængde</label>
            <br />
            <label>{{SelectedItem.amountLeft}}</label>
          </b-col>
        </b-row>
      </b-form>
    </b-modal>

    <b-modal
      id="editItemModal"
      size="lg"
      :title="SelectedItem.itemBrand"
      v-model="showEditItemModal"
      no-close-on-backdrop
      @ok="editItem"
    >
      <b-form>
        <b-row>
          <b-col lg="6" style="margin-bottom:15px">
            <label>Mærke</label>
            <b-form-input v-model="SelectedItem.brand" type="text" required placeholder="Mærke"></b-form-input>
          </b-col>
          <b-col lg="6">
            <label>Model</label>
            <b-form-input v-model="SelectedItem.model" type="text" required placeholder="Model"></b-form-input>
          </b-col>
          <b-col lg="6" style="margin-bottom:15px">
            <label>Beskrivelse</label>
            <b-form-input v-model="SelectedItem.description" type="text" placeholder="Beskrivelse"></b-form-input>
          </b-col>
          <b-col lg="6">
            <label>Kategori</label>
            <b-form-select
              v-model="dropdownSelected"
              :options="Categories"
              value-field="id"
              text-field="value"
            ></b-form-select>
          </b-col>
          <b-col lg="4">
            <b-button style="width:130px" v-b-modal.selectImageModal>Vælg billede</b-button>
            <br />
            <b-button style="width:130px; margin-top:15px" v-b-modal.UploadImageModal>Upload billede</b-button>
          </b-col>
          <b-col lg="2">
            <b-img
              style="height:105px"
              v-if="showEditItemModal"
              fluid
              :src="SelectedItem.image.imageUri"
            ></b-img>
            <b-img
              v-else
              fluid
              src="https://inventoryimagestorage.blob.core.windows.net/public-images/ImagePlaceholder.png"
            ></b-img>
          </b-col>
          <b-col lg="6">
            <label>Mængde</label>
            <b-form-input
              :readonly="SelectedItem.barcodes.length == 0 && SelectedItem.amountLeft == 0"
              v-model="SelectedItem.amountLeft"
              type="number"
              placeholder="Mængde"
            ></b-form-input>
          </b-col>
        </b-row>
      </b-form>
    </b-modal>

    <b-modal
      id="deleteSelectedItemModal"
      size="lg"
      v-model="showDeleteSelectedItemModal"
      no-close-on-backdrop
      :title="selectedItemModalTitle"
    >
      <b-row>
        <b-col lg="12">
          <p
            style="font-size:20px"
          >Er du sikker på at du vil fjerne {{SelectedItem.brand}} {{SelectedItem.model}} fra lageret?</p>
        </b-col>
      </b-row>
      <template v-slot:modal-footer>
        <b-button class="float-right" @click="showDeleteSelectedItemModal = false">Annuller</b-button>
        <b-button
          style="margin-left:10px"
          class="float-right"
          variant="danger"
          @click="deleteItem"
        >Ja</b-button>
      </template>
    </b-modal>

    <b-modal
      id="selectImageModal"
      size="lg"
      title="Vælg billede"
      v-model="showSelectImageModal"
      no-close-on-backdrop
      scrollable
      ok-only
    >
      <template v-slot:modal-header>
        <b-row style="width:100%">
          <b-col lg="3">
            <h5>Vælg billede</h5>
          </b-col>
          <b-col lg="9">
            <b-form-input type="search" v-model="imageSearch" placeholder="Skriv for at søge..."></b-form-input>
          </b-col>
        </b-row>
      </template>
      <b-form-group>
        <b-row>
          <b-col lg="3" v-for="image in SearchedImages" :key="image.imageId">
            <b-form-radio
              v-model="selectedImage"
              class="item-select"
              :value="image"
              :class="{'item-select-active' :SelectedImage == image}"
            >
              <label class="imageSize">{{image.imageName}}</label>
              <b-img class="img-hover" style="max-height:105px; max-width:105px" :src="image.imageUri"></b-img>
            </b-form-radio>
          </b-col>
        </b-row>
      </b-form-group>
      <template v-slot:modal-footer>
        <div class="w-100">
          <p class="float-left">Valgt billede navn: {{SelectedImage.imageName}}</p>
          <b-button
            style="margin-left:10px"
            class="float-right"
            variant="primary"
            @click="okSelectImageModal"
          >Ok</b-button>
          <b-button
            style="margin-left:10px"
            class="float-right"
            @click="closeSelectImageModal"
          >Annuller</b-button>
        </div>
      </template>
    </b-modal>

    <b-modal id="UploadImageModal" size="xl" title="Upload billede" v-model="showUploadImageModal">
      <b-form-group>
        <b-row>
          <b-col lg="12" style="margin-bottom:30px">
            <label>Navn</label>
            <b-form-input :state="NoImageName" v-model="uploadImageName" type="text" required placeholder="Navn"></b-form-input>
            <b-form-invalid-feedback :state="NoImageName">Husk at give billedet et navn</b-form-invalid-feedback>
          </b-col>
          <b-col lg="12">
            <div class="file-upload" :class="imageUploadBorder">
              <div v-if="!isUploaded" class="file-upload-text" ref="uploadedImageText">
                <b-img
                  class="fileUploadImg"
                  src="https://inventoryimagestorage.blob.core.windows.net/public-images/FileUpload.png"
                ></b-img>Vælg et billede...
              </div>
              <b-img v-else style="height:inherit" :src="uploadedImageURL"></b-img>
              <input
                type="file"
                id="uploadedImage"
                ref="uploadedImage"
                v-on:change="handleFileUpload()"
              />
            </div>
          </b-col>
        </b-row>
      </b-form-group>
      <template v-slot:modal-footer>
        <div class="w-100">
          <b-button
            style="margin-left:10px"
            class="float-right"
            variant="primary"
            @click="uploadImage"
          >Ok</b-button>
          <b-button class="float-right" @click="closeModal">Annuller</b-button>
        </div>
      </template>
    </b-modal>

    <addBarcodeToItemModal
      :item="selectedItem"
      v-model="showAddBarcodeToItem"
      @updateItemBarcodes="getItems"
    ></addBarcodeToItemModal>
  </b-container>
</template>

<script>
import smoothReflow from "vue-smooth-reflow";

var timer;

export default {
  name: "ConsumptionInventoryManagement",
  mixins: [smoothReflow],
  data() {
    return {
      itemBrand: "",
      itemModel: "",
      itemDescription: "",
      itemBarcode: "",
      itemAmount: "",
      dropdownSelected: null,

      requestURL: "",
      modalTitle: "",
      addItemsBtn: "",
      inventoryManagement: false,

      imageUploadBorder: "fileBorder",

      imageSearch: "",
      selectedImage: null,
      uploadImageName: "",
      uploadedImage: "",
      isUploaded: false,
      uploadedImageURL: "",
      selectedItem: {},

      selectedItemModalTitle: "",

      showMoreAboutItem: false,
      showAddItemModal: false,
      showAddBarcodeToItem: false,
      showEditItemModal: false,
      showSelectImageModal: false,
      showUploadImageModal: false,
      showDeleteSelectedItemModal: false,

      fields: [
        { key: "image", label: "" },
        { key: "brand", label: "Mærke", sortable: true },
        { key: "model", label: "Model", sortable: true },
        { key: "category.categoryName", label: "Kategori", sortable: true },
        { key: "amountLeft", label: "Mængde", sortable: true },
        { key: "button", label: "" }
      ],
      isFetching: true,
      failedFetches: 0,
      search: "",
      currentPage: 1,
      totalRows: 0,
      perPage: 10,
      sortBy: "",
      activeModal: "",
      Items: Array,
      notFoundMessage: "",
      
    };
  },
  computed: {
    NoImageName() {
      if (this.uploadImageName != '') {
        return true
      } else {
        return false
      }
    },
    ConsumptionItems() {
      return this.$store.getters["inventoryManagement/getItems"];
    },

    ConsumptionItemAmount() {
      return this.$store.getters["inventoryManagement/getItems"].length;
    },

    Images() {
      return this.$store.getters["inventoryManagement/getImages"];
    },

    Categories() {
      return this.$store.getters["inventoryManagement/getCategories"];
    },

    SelectedItem: {
      cache: false,
      get() {
        if (Object.entries(this.selectedItem).length === 0) {
          return {
            barcodes: [],
            inventory: {
              inventoryType: ""
            },
            category: {
              categoryName: ""
            },
            image: {
              imageUri: ""
            }
          };
        } else {
          return this.selectedItem;
        }
      }
    },

    SelectedImage() {
      if (this.selectedImage != null) {
        return this.selectedImage;
      } else {
        return {
          imageId: 0,
          imageName: "Intet billede valgt",
          imageUri:
            "https://inventoryimagestorage.blob.core.windows.net/public-images/ImagePlaceholder.png"
        };
      }
    },

    SearchedImages() {
      if (this.imageSearch == "") {
        return this.Images;
      } else {
        let _imagesList = [];
        for (var i = 0; i < this.Images.length; i++) {
          if (
            this.Images[i].imageName
              .toLowerCase()
              .includes(this.imageSearch.toLowerCase())
          ) {
            _imagesList.push(this.Images[i]);
          }
        }
        return _imagesList;
      }
    }
  },
  methods: {
    ShowTable() {
      this.getCategories();
      this.getImages();
      this.getItems();
    },
    getCategories() {
      this.$store
        .dispatch("inventoryManagement/getCategories")
        .then(() => {
          // eslint-disable-next-line no-console
          console.log(
            "Got some data, now lets show something in this component"
          );
          this.failedFetches = 0;
        })
        .catch(() => {
          // eslint-disable-next-line no-console
          console.log("no categories found");
          this.totalRows = 0;
          this.failedFetches++;
          if (this.failedFetches <= 5) {
            timer = setTimeout(() => {
              this.getCategories();
            }, 5000);
          } else {
            this.failedFetches = 0;
            this.notFoundMessage = "Der blev ikke fundet nogen kategorier...";
          }
        });
    },
    getImages() {
      this.$store
        .dispatch("inventoryManagement/getImages")
        .then(() => {
          // eslint-disable-next-line no-console
          console.log(
            "Got some data, now lets show something in this component"
          );
          this.failedFetches = 0;
        })
        .catch(() => {
          // eslint-disable-next-line no-console
          console.log("no images found");
          this.totalRows = 0;
          this.failedFetches++;
          if (this.failedFetches <= 5) {
            timer = setTimeout(() => {
              this.getImages();
            }, 5000);
          } else {
            this.failedFetches = 0;
            this.notFoundMessage = "Der blev ikke fundet nogen billeder...";
          }
        });
    },
    uploadImage() {
      let formData = new FormData();
      formData.append("file", this.uploadedImage);
      formData.append("imageName", this.uploadImageName);

      if (this.uploadImageName == '') {
        return
      }


      this.$store
        .dispatch("inventoryManagement/uploadImage", formData)
        .then(resolve => {
          this.selectedImage = resolve;
          // eslint-disable-next-line no-console
          console.log("SUCCESS!!");
          this.closeUploadImageModal;
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    },
    handleFileUpload() {
      this.imageUploadBorder = "";
      this.uploadedImage = this.$refs.uploadedImage.files[0];
      this.uploadedImageURL = URL.createObjectURL(this.uploadedImage);

      this.isUploaded = true;
    },
    okSelectImageModal() {
      if (this.selectedImage != null) {
        this.showSelectImageModal = false;
      } else {
        alert("Error");
      }
    },
    closeSelectImageModal() {
      this.selectedImage = null;
      this.showSelectImageModal = false;
    },
    getItems() {
      this.isFetching = true;
      this.$store
        .dispatch("inventoryManagement/getConsumptionItems", 2)
        .then(() => {
          // eslint-disable-next-line no-console
          console.log(
            "Got some data, now lets show something in this component"
          );
          this.totalRows = this.ConsumptionItemAmount;
          this.Items = this.ConsumptionItems;
          this.isFetching = false;
          this.failedFetches = 0;
        })
        .catch(() => {
          // eslint-disable-next-line no-console
          console.log("no items found");
          this.Items = [];
          this.totalRows = 0;
          this.failedFetches++;
          if (this.failedFetches <= 5) {
            timer = setTimeout(() => {
              this.getItems();
            }, 5000);
          } else {
            this.isFetching = false;
            this.failedFetches = 0;
            this.notFoundMessage =
              "Der blev ikke fundet nogen ting i lageret...";
          }
        });
      this.modalTitle = "Tilføj ting til lager";
      this.addItemsBtn = "Tilføj ting til lager";
      this.inventoryManagement = false;
    },
    addItem() {
      this.$store
        .dispatch("inventoryManagement/addConsumptionItem", {
          inventoryId: 2,
          brand: this.itemBrand,
          model: this.itemModel,
          description: this.itemDescription,
          categoryId: this.dropdownSelected,
          imageId: this.selectedImage.imageId
        })
        .then(() => {
          this.itemBrand = "";
          this.itemModel = "";
          this.itemDescription = "";
          this.selectedImage = null;

          this.getItems();
        })
        .catch(reject => {
          alert(reject);
        });
    },
    addBarcodeToItem(item) {
      Object.assign(this.selectedItem, item);
      this.showAddBarcodeToItem = true;
    },
    fillEditItemModal(item) {
      Object.assign(this.selectedItem, item);
      this.dropdownSelected = item.category.categoryID;
      this.showEditItemModal = true;
    },
    editItem() {
      this.$store
        .dispatch("inventoryManagement/editConsumptionItem", {
          itemId: this.selectedItem.itemId,
          inventoryId: 2,
          brand: this.selectedItem.brand,
          model: this.selectedItem.model,
          description: this.selectedItem.description,
          categoryId: this.dropdownSelected,
          imageId: this.selectedItem.image.imageId,
          amountLeft: parseInt(this.selectedItem.amountLeft)
        })
        .then(() => {
          this.getItems();
          this.showEditItemModal = false;
        })
        .catch(reject => {
          this.getItems();
          alert(reject);
        });
    },
    selectedItemModal(item, modalToOpen) {
      this.selectedItemModalTitle = item.brand + " - " + item.model;
      Object.assign(this.selectedItem, item);
      this.selectedImage = this.selectedItem.image;
      this.dropdownSelected = item.category.categoryID;

      if (modalToOpen == "About") this.showMoreAboutItem = true;
      if (modalToOpen == "Delete") this.showDeleteSelectedItemModal = true;
    },
    deleteItem() {
      this.$store
        .dispatch("inventoryManagement/deleteConsumptionItem", {
          itemId: this.selectedItem.itemId,
          inventoryId: 2
        })
        .then(() => {
          this.totalRows--;
          this.getItems();
        });
      this.showDeleteSelectedItemModal = false;
    },
    closeModal() {
      this.dropdownSelected = null;
      this.itemBrand = "";
      this.itemModel = "";
      this.itemDescription = "";
      this.itemBarcode = "";

      this.showUploadImageModal = false;
      this.uploadImageName = "";
      this.uploadedImageURL = "";
      this.imageUploadBorder = "fileBorder";
      this.isUploaded = false;

      timer = setTimeout(() => {
        this.selectedImage = null;
      }, 250);
    },
    cancel() {
      clearTimeout(timer);
      this.failedFetches = 0;
      this.isFetching = false;
    },
    onFilter(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  created() {
    this.ShowTable();
  },
  mounted() {
    this.$smoothReflow({
      el: "#inventoryTable"
    });
    this.$smoothReflow({
      el: ".tablecontainer"
    });
  }
};
</script>